/*======================= BASE STYLES =======================*/
.about__container {
    display: grid;
    grid-template-columns: 1fr;
    gap: 2rem;
    padding: 1rem;
}

.about__me {
    width: 80%;
    margin: 0 auto;
    aspect-ratio: 1 / 1;
    border-radius: 2rem;
    background: linear-gradient(
        45deg,
        transparent,
        var(--color-primary),
        transparent
    );
    display: grid;
    place-items: center;
}

.about__me img {
    height: 100%;
    border-radius: 2rem;
    overflow: hidden;
    transform: rotate(10deg);
    transition: var(--transition);
}

.about__me img:hover {
    transform: rotate(0);
}

.about__cards {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 1rem;
}

.about__card {
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 1.5rem;
    text-align: center;
}

.about__card:hover {
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.about__icon {
    color: var(--color-primary);
    font-size: 1.2rem;
    margin-bottom: 0.8rem;
}

.about__card h5 {
    font-size: 0.9rem;
}

.about__card small {
    font-size: 0.7rem;
    color: var(--color-light);
}

.about__content {
    text-align: center;
    margin-top: 1.5rem;
}

.about__content p {
    margin: 1.5rem 0;
    color: var(--color-light);
}

/*======================= MEDIA QUERIES =======================*/

/* Medium Devices (Tablets) */
@media screen and (min-width: 601px) and (max-width: 1024px) {
    .about__container {
        grid-template-columns: 1fr 1fr;
        gap: 1.5rem;
    }

    .about__me {
        width: 60%;
        margin: 2rem auto;
    }

    .about__cards {
        grid-template-columns: 1fr 1fr;
        gap: 1.5rem;
    }

    .about__content p {
        margin: 1.5rem 0 2rem;
    }
}

/* Large Devices (Desktops) */
@media screen and (min-width: 1025px) {
    .about__container {
        grid-template-columns: 35% 50%;
        gap: 15%;
    }

    .about__me {
        width: 90%;
        margin: 0;
    }

    .about__cards {
        grid-template-columns: repeat(3, 1fr);
        gap: 2rem;
    }

    .about__content p {
        margin: 2rem 0 2.6rem;
    }
}
